import { NextRequest, NextResponse } from 'next/server'
import supabaseAdmin from '@/services/supabase/admin'
import { createServerClient } from '@/services/supabase/server'
import { logger } from '@/utils/logger'
import { FileUploadValidator } from '@/utils/fileUploadValidation'
import { secureUploadRoute } from '@/middleware/security'

/**
 * Generic File Upload Handler
 * 
 * Eliminates DRY violations by centralizing file upload logic.
 * Used by avatar, banner, and other file upload APIs.
 * 
 * 🔒 SECURITY: Authentication required, comprehensive validation
 * ♻️ REFACTORED: Eliminates 66 lines of duplicate code per API route
 */

export interface FileUploadConfig {
  bucketName: string
  logPrefix: string
  validationType: 'avatar' | 'banner' | 'document'
}

export async function createFileUploadHandler(config: FileUploadConfig) {
  return async function handleFileUpload(req: NextRequest): Promise<NextResponse> {
    // Apply security middleware first
    const securityResult = await secureUploadRoute(req)
    if (securityResult) {
      return securityResult
    }

    try {
      // CRITICAL SECURITY FIX: Mandatory authentication verification
      const supabase = await createServerClient()
      const { data: { user }, error: authError } = await supabase.auth.getUser()

      if (!user || authError) {
        logger.error(`${config.logPrefix} Upload failed: Authentication required`, { error: authError?.message }, 'Security')
        return NextResponse.json({ error: 'Authentication required' }, { status: 401 })
      }

      const formData = await req.formData()
      const file = formData.get('file') as File | null
      const requestedUserId = formData.get('userId') as string | null

      // CRITICAL SECURITY FIX: Verify user can only upload for themselves
      if (!requestedUserId || requestedUserId !== user.id) {
        logger.warn(`${config.logPrefix} Upload blocked: User ${user.id} attempted to upload for user ${requestedUserId}`, {}, 'Security')
        return NextResponse.json(
          { error: 'Cannot upload files for other users' },
          { status: 403 }
        )
      }

      // CRITICAL SECURITY FIX: Sanitize userId to prevent path traversal
      const sanitizedUserId = user.id.replace(/[^a-zA-Z0-9-]/g, '')
      if (sanitizedUserId !== user.id) {
        logger.warn(`${config.logPrefix} Upload blocked: Invalid user ID format for user ${user.id}`, {}, 'Security')
        return NextResponse.json(
          { error: 'Invalid user ID format' },
          { status: 400 }
        )
      }

      // CRITICAL SECURITY FIX: Check user permissions for file uploads
      const { data: userProfile, error: profileError } = await supabase
        .from('profiles')
        .select('id, upload_enabled')
        .eq('id', user.id)
        .single()

      if (profileError || !userProfile?.upload_enabled) {
        logger.warn(`${config.logPrefix} Upload blocked: Uploads disabled for user ${user.id}`, {}, 'Security')
        return NextResponse.json(
          { error: 'File uploads disabled for your account' },
          { status: 403 }
        )
      }

      if (!file) {
        logger.warn(`${config.logPrefix} Upload failed: No file provided for user ${user.id}`, {}, 'Security')
        return NextResponse.json({ error: 'No file provided' }, { status: 400 })
      }

      // ── Centralized File Validation ──────────────────────────────────────────────
      const buffer = Buffer.from(await file.arrayBuffer())
      const validationConfig = FileUploadValidator.getConfig(config.validationType)
      const validation = await FileUploadValidator.validateFile(file, buffer, validationConfig)
      
      if (!validation.valid) {
        return NextResponse.json({ error: validation.error }, { status: 400 })
      }

      // ── Secure Image Processing ──────────────────────────────────────────────
      const processedResult = await FileUploadValidator.processImage(buffer, validationConfig, 'webp')
      const processedImage = processedResult.buffer

      // ── Generate Secure File Path ──────────────────────────────────────────────
      const timestamp = Date.now()
      const filePath = `${sanitizedUserId}/${timestamp}.webp`

      // ── Upload to Supabase Storage ──────────────────────────────────────────────
      const { error: uploadError } = await supabaseAdmin.storage
        .from(config.bucketName)
        .upload(filePath, processedImage, {
          contentType: 'image/webp',
          upsert: true,
          cacheControl: '3600'
        })

      if (uploadError) {
        logger.error(`${config.logPrefix} Upload failed:`, { error: uploadError.message }, 'Upload')
        return NextResponse.json({ error: 'Upload failed' }, { status: 500 })
      }

      // ── Get public URL ──────────────────────────────────────────────────────────
      const { data } = supabaseAdmin.storage.from(config.bucketName).getPublicUrl(filePath)

      // CRITICAL SECURITY FIX: Comprehensive audit logging
      logger.info(`${config.logPrefix} upload completed for user ${user.id}`, {
        filePath,
        fileSize: processedImage.length,
        originalFileName: file.name,
        fileType: file.type,
        dimensions: validationConfig.dimensions,
        userAgent: req.headers.get('user-agent'),
        ip: req.headers.get('x-forwarded-for') || req.headers.get('x-real-ip') || 'unknown'
      }, 'Security')

      return NextResponse.json({
        publicUrl: data.publicUrl,
        size: processedImage.length,
        dimensions: validationConfig.dimensions,
        processed: true,
        securityValidated: true,
        userId: user.id // Return the verified userId for client confirmation
      })

    } catch (error: any) {
      logger.error(`${config.logPrefix} Upload error:`, { error: error.message }, 'Security')
      return NextResponse.json({ error: 'Upload failed' }, { status: 500 })
    }
  }
}